import React, { Component, Suspense, lazy } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
// import Home from '../static/js/home/Home';
// import Artist from '../static/js/artist/Artist';
// import About from '../static/js/about/About';
// import Shop from '../static/js/shop/Shop';
// import Contact from '../static/js/contact/Contact';


const Home = lazy(() => import('../static/js/home/Home'));
const About = lazy(() => import('../static/js/about/About'));
const Artist = lazy(() => import('../static/js/artist/Artist'));
const Shop = lazy(() => import('../static/js/shop/Shop'));
const Contact = lazy(() => import('../static/js/contact/Contact'));

class App extends Component {
  render() {
    return (
      <div className="App">
        <Suspense fallback={<div>Loading...</div>}>
          <Route exact path="/" component={Home}/>
          <Switch>
            <Route path="/artist/:name" component={Artist}/>
            <Route path="/artist" component={Artist}/>
          </Switch>
          <Route path="/about" component={About}/>
          <Route path="/shop" component={Shop}/>
          <Route path="/contact" component={Contact}/>
        </Suspense>
      </div>
    );
  }
}

export default App;